<template>
    <div class="container">
        <h1>This is home page.</h1>

        <!--star route table-->
        <div class="star-route">
            <p v-if="star_route_stop_eta_list.length == 0">沒有已儲存路線</p>
            <table class="table">
                <thead>
                    <th>路線</th>
                    <th>方向</th>
                    <th>時間</th>
                </thead>
                <tbody>
                    <!--KMB-->
                    <router-link tag="tr" v-for="route in star_route_stop_eta_list" :key="route.id" :to="route_link('KMB', route.route, route.bound, route.service_type)">
                        <td>{{ route.route }}</td>
                        <td>
                            <div>
                                往 {{ route.dest_tc }}
                            </div>
                            <div>
                                {{ route.name_tc }}
                            </div>
                        </td>
                        <td></td>
                    </router-link>

                


                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import * as util from '../util/util'

//let KMBRouteList;

export default {
    name: 'HomePage',
    data(){
        return {
            full_route_list: [],
            full_route_stop_list: [],
            full_stop_list: [],
            star_list: [],
            star_route_stop_eta_list: [],
            show_detail: true,
        }
    },
    mounted() {
        let that = this;
        (async () => {
            await util.getKMBRouteList();
            await util.getKMBStopList();
            await util.getKMBRouteStopList();
            that.full_route_list = JSON.parse(localStorage.getItem("KMBRouteList"));
            that.full_stop_list = JSON.parse(localStorage.getItem("KMBStopList"));
            that.full_route_stop_list = JSON.parse(localStorage.getItem("KMBRouteStopList"))    ;
            that.star_list = JSON.parse(localStorage.getItem("KMBRouteStopStar"));
            

            // map star_list with route information
            that.star_route_stop_eta_list = that.star_list.map((e) => {

                let route_match = that.full_route_list.find(route => (
                    route.route == e.route &&
                    route.bound == e.bound &&
                    route.service_type == e.service_type
                ));

                if (route_match){
                    e.orig_tc = route_match.orig_tc;
                    e.dest_tc = route_match.dest_tc;
                }

                return e;
            });
            
            // map star_list with route stop information
            that.star_route_stop_eta_list = that.star_route_stop_eta_list.map((e) => {

                let route_stop_match = that.full_route_stop_list.find(route_stop => (
                    route_stop.route == e.route &&
                    route_stop.bound == e.bound &&
                    route_stop.service_type == e.service_type &&
                    route_stop.seq == e.seq
                ));

                if (route_stop_match){
                    e.stop = route_stop_match.stop;
                }
                
                return e;
            });

            // map star_list with stop information
            that.star_route_stop_eta_list = that.star_route_stop_eta_list.map((e) => {

                let stop_match = that.full_stop_list.find(stop => (
                    stop.stop == e.stop
                ));

                if (stop_match){
                    e.name_tc = stop_match.name_tc;
                }

                return e;
            });

            // get eta for each stop

            let debug = JSON.stringify(that.star_route_stop_eta_list);
            console.log("debug");
            console.log(debug);

        })();
        
    },
    // created: {

    // },
    methods: {
        route_link: function(company, route, bound, service_type) {
            console.log("route_link click");

            console.log(company, route, bound, service_type);
            return '/' + company + '/' + route + '/' + bound + '/' + service_type;
        }
    }
}

/* Local storage:
    KMBRouteList_timestamp: store the last updated timestamp of the KMBRouteList in localStorage
    KMBRouteList: store the KMBRouteList retrieved from API
 */
</script>

